.main-header{
	background-color: #fff;
	padding-bottom: 22px;
	padding-top: 22px;
	position: relative;
	.desktop-menu{
		ul.menu{
			>li{
				@include transition(0.5s);
				margin-left: 14px;
				&:first-child{
					margin-left: 0;
				}
				&.current-menu-item{
					a{
						color: #dfad00;
					}
				}
				&.current-menu-ancestor{
					a{
						color: #dfad00;
					}
				}
				&:hover{
					a{
						color: #dfad00;
					}
				}
				ul.sub-menu{
					@include transition(0.5s);
					width: 226px;

					li{
						a{
							color: #fff;
							background-color: rgba(224, 182, 34, 0.7);
							text-transform: none;
							@include transition(0.5s);
							&:hover,{
								background-color:#cb9f09;
							}
						}
						&.current-menu-item{
							a{
								background-color:#cb9f09;
							}
						}
					}
				}
			}
			a{
				border-bottom: 0;
				font-family: $font-main-light;
				font-size: 18px;
				text-transform: capitalize;
			}
		}
	}
	&.uk-sticky-fixed{
		padding-bottom: 0;
    	padding-top: 5px;
    	.main-logo{
    		>a{
    			width: 165px;
    		}
    	}
	}
}

.footer{
	position: fixed;
	background-color: #e7e7e7;
	border-top: 1px solid #000;
	color: #000;
	a{
		color : #000;
		&:hover{
			color: #dfad00;
		}
	}
	.left-content{
		.contact-title, .address{
			display: inline-block;
			float: left;
		}
		.uk-flex{
			@media (max-width: 619px){
				display: inline-block;
				width: 100%;
			}
		}
		.contact-title{
			@media (max-width: 991px){
				width: 33.33%;
			}
			@media (max-width: 619px){
				width: 100%;
			}
			color: #363636;
			font-family: $font-main-light;
			font-size: 18px;
			@media (min-width: 992px){
				padding-right: 70px;
			}
		}
		.address, .phones{
			@media (max-width: 991px){
				width: 33.33%;
			}
			@media (max-width: 619px){
				float: left;
   				width: 50%;
			}
			font-size: 10px;
    		line-height: 16px;
    		position: relative;
			&:before{
				@media (min-width: 620px){
					content: "";
				    font-family: fontawesome;
				    font-size: 18px;
				    left: -20px;
				    position: absolute;
				}
			}
		}
		.address{
			@media (min-width: 992px){
				padding-right: 60px;
			}
		}
		.phones{
			&:before{
				@media (min-width: 620px){
					content: "";
				}
			}
		}
		.telephone{
			li{
				list-style-type: none;
			}
		}
	}
	.outlink{
		@media (max-width: 991px){
			display: none;
		}
		a{	
			font-size: 15px;
			color: #363636;
			width: 100%;
			text-align: right;
			img{

			}
		}
	}

	.right-content{
		text-align: right;
		@media (min-width: 1200px){
			float: right;
			text-align: left;
		}
		.social, a{
			color: #ff0000;
		}
		.social {
			float: left;
			overflow: auto;
			margin-bottom: 0;
			li {
				list-style-type: none;
				float: left;
				i {
					background: #205D7A;
					 border-radius: 20px;
				    color: #ffffff;
				    font-size: 14px;
				    height: 29px;
				    margin-right: 10px;
				    padding-top: 19%;
				    text-align: center;
				    width: 30px;
				}
				.fa-facebook {
					background:#3b5998
				} 
				.fa-linkedin {
					background:#007bb6
				}
				.fa-twitter {
					background:#00aced
				}
				.google-plus {
					background:#dd4b39
				}
			}
		}
		.copyright{
			@media (max-width: 991px){
				display: none;
			}
			font-size: 11px;
			color: #363636;
			padding-left: 3px;
			@media (min-width: 1200px){
				padding-right: 20px;
			}
		}
		> a{
			@media (max-width: 991px){
				display: none;
			}
			font-size: 11px;
			color: #363636;
		}
	}
}

//frontpage
.home{
	overflow: hidden;
	.main-header{
		@include transition(0.5s);
		position: absolute;
		opacity: 0.7;
    	top: -113px;
	}
	.footer{
		@include transition(0.5s);
 		bottom: -78px;
 		opacity: 0.7;
	}
}
.home.pace-done{
	.main-header{
		opacity: 1;
		top: 0;
	}
	.footer{
		opacity: 1;
		bottom: 0;
	}
}

.sidepanel{
	background-color: rgba(223, 173, 0, 0.75);
	.menu{
		> li{
			.dropdown-toggle{
				>*{
					@include transition(0.5s);
				}
			}
			&.open{
				.dropdown-menu{
					background-color: rgba(204, 204, 204, 0.75);
				}
				.nav-control{
					&:before{
						content: "";
					}
				}
				.dropdown-toggle{
					>*{
						transform: rotate(45deg);
					}
				}
			}
		}
	}
	.mobile-menu-btn{
		padding: 0;
	}
}

.main-header{
	.mobile-menu-btn{
		position: relative;
		em{
			color: #000;
		    font-style: normal;
		    opacity: 0;
		    position: absolute;
		    right: 10px;
		    top: 9px;
		    @include transition(0.5s);
		}
		@media (min-width: 400px){
			&:hover, &.toggle-mobile-menu{
				em{
					color: #dfad00;
					opacity: 1;
		    		right: 55px;
		    	}
			}
		}
	}
}

.tparrows{
	&:before{
		opacity: 0.5;
	}
}