.wrapper-holder{
	background-color: #e7e7e7;
}
body{
	color: #000;
}
.page{
	.default-container{
		padding-bottom: 15px;
		padding-top: 15px;
		@media (min-width: 768px){
			padding-top: 42px;
			padding-bottom: 42px;
		}
		.page-title{
			@media (min-width: 768px){
				padding-bottom: 28px;
			}
			h1{
				color: #dfad00;
				font-family: Georgia, serif;
				font-size: 25px;
				@media (min-width: 768px){
					font-size: 48px;
				}
			}
		}
		ul{
			list-style-image: url(data:image/png;base64,);
			li{
				color: #000;
				font-size: 14px;
				padding-left: 5px;
				padding-bottom: 15px;
				// @media (min-width: 768px){
				// 	padding-bottom: 23px;
				// 	// padding-left: 24px;
				// }
			}
		}
		p{
			color: #000;
			font-size: 14px;
		}
	}
	&.page-id-360{
		.wrapper-holder{
			background-image: url("../img/visionbg.gif");
		    background-position: 79% 83%;
		    background-repeat: no-repeat;
		}
	}
	div.year, div.single{
		padding-left: 55px;
		padding-right: 55px;
		padding-top: 15px;
		.slick-next::before{
			content: "";
			color: #1b1b1b;
			float: right;
			font-family: FontAwesome;
			font-size: 48px;
		}
		.slick-prev::before{
			content: "";
			color: #1b1b1b;
			font-family: FontAwesome;
			font-size: 48px;
		}
		.slick-next, .slick-prev{
			height: 48px;
			width: 48px;
			&:before{
				line-height: 1.25;
			}
		}
		span{
			font-family: Georgia,serif;
			font-size: 18px;
			color: #e1a54f;
			text-align: center;
			cursor: pointer;
			@include transition(0.5s);
			&.slick-current{
				transform: scale(1.3);
			}
			&:focus{
				outline: none;
			}
		}
	}
	div.single{
		span{
			cursor: text;
		}
	}
	div.year-details, div.single-details{
		margin-top: 20px;
		@media (min-width: 768px){
			margin-top: 30px;
		}
		ul{
			list-style-image: url(data:image/png;base64,);
			li{
				color: #000;
				padding-left: 5px;
				padding-bottom: 15px;
				text-align: justify;
				// @media (min-width: 768px){
				// 	padding-bottom: 23px;
				// 	// padding-left: 24px;
				// }
			}
		}
		.slick-slide{
			&:focus{
				outline: none;
			}
		}
	}
}

.page-template-template-tab{
	.inner-page-layout{
		padding-bottom: 15px;
		padding-top: 15px;
		@media (min-width: 768px){
			padding-top: 45px;
			padding-bottom: 80px;
		}
		.page-title{
			@media (min-width: 768px){
				padding-bottom: 28px;
			}
			h1{
				color: #dfad00;
				font-family: Georgia, serif;
				font-size: 25px;
				@media (min-width: 768px){
					font-size: 48px;
				}
			}
		}
		.sidebar{
			h3{
				color: #dfad00;
				font-family: Georgia,serif;
				font-size: 16px;
				margin-bottom: 5px;
			}
			.uk-tab, .uk-nav{
				margin-top: 0;
				> * {
					padding-left: 0;
					> a{
						color: #000;
						font-size: 14px;
						padding-left: 0;
						padding-right: 0;
						border-bottom: 0;
						text-align: left;
						text-transform: capitalize;
						&:hover{
							color: #dfad00;
						}
					}
				}
				>li.uk-active{
					a{
						color: #dfad00;
					}
					&.initial{
						a{
							color: #1b1b1b;
							&:hover{
								color: #dfad00;
							}
						}
					}
				}
				+ h3{
					margin-top: 28px;
				}
			}
			.accollades{
				padding-left: 0;
				li{
					padding-top: 5px;
					padding-bottom: 5px;
					color: #000;
					&:hover{
						cursor: pointer;
						color: #dfad00;
					}
				}
			}
			.awards{
				list-style: none;
				padding-left: 0;
				> li{
					> a{
						border: 0;
						color: #000;
					}
					&:hover{
						a{
							color: #dfad00;
						}
					}
				}
			}
		}
		.content{
			* + .uk-grid-margin-small, .uk-grid + .uk-grid-small, .uk-grid-small > .uk-grid-margin{
				margin-top: 40px;
			}
			.accolade-content{
				.uk-flex{
					img{
						@include transition(0.5s);
					}
					&.active{
						img{
							transform: scale(1.3);
						}
					}
				}
			}
			ul.uk-switcher{
				@media (max-width: 991px){
					padding-left: 0;
				}
				li{
					h2{
						color: #dfad00;
						font-size: 25px;
						@media (min-width: 768px){
							font-size: 48px;
							margin-bottom: 43px;
						}
						font-family: $font-main-extra-bold;
					}
					p{
						+ ul{
							padding-top: 22px;
						}
					}
				}
				ul{
					list-style-image: url(data:image/png;base64,);
					padding-left: 12px;
					li{
						color: #646464;
						padding-left: 24px;
					}					
				}
			}
		}
	}
}
.uk-modal-page{
	.page-template-template-tab{
		.accollades{
			li{
				&.uk-active{
					cursor: pointer;
					color: #dfad00;
				}
			}
		}
	}
}

.page-template-template-partners,
.page-template-template-associates,
.page-template-template-sen_associates,
.page-template-template-contact,
.single-partners,
.single-associates,
.single-sen_associates,
.page-template-template-principal{
	.partners-layout,
	.contact-layout{
		h1{
			color: #dfad00;
			font-family: Georgia, serif;
			font-size: 25px;
			padding-bottom: 15px;
    		padding-top: 15px;
			@media (min-width: 768px){
				font-size: 48px;
				padding-bottom: 42px;
    			padding-top: 37px;
			}
			
		}

		.sidebar{
			.lawyer-container{
				margin-top: 26px;
				border-top: 3px solid #fff;
				padding-top: 25px;
				span{
					font-family: $font-main-bold;
					font-size: 16px;
					color: #dfad00;
				}
				.slick-vertical{
					.slick-slide{
						border: 0;
						a{
							color: #1b1b1b;
							&:hover{
								color: #dfad00;
							}
						}
						&.slick-current{
							a{
								color: #dfad00;
							}
						}
					}	
				}
				.lawyers{
					a{
						color: #1b1b1b;
						&:hover, &.active{
							color: #dfad00;
						}
					}
				}
			}
		}
		
		.content{
			@media (min-width: 992px){
				padding-top: 57px;
			}
			padding-bottom: 57px;
			.title{
				color: #363636;
				font-size: 16px;
				font-weight: 700;
				+ p{
					margin-top: 15px;
					@media (min-width: 992px){
						margin-top: 30px;
					}
				}
			}
			.contact-info{
				margin-top: 15px;
				@media (min-width: 992px){
					margin-top: 30px;
				}
				span{
					color: #363636;
					font-size: 16px;
					font-weight: 700;
				}
				+ p{
					margin-top: 30px;
				}
				.numbers{
					.divider{
						font-weight: lighter;
						margin-left: 2px;
						margin-right: 5px;
						&:last-of-type{
							display: none;
						}
					}
				}
			}
			.lawyer-content{
				em{
					color: inherit;
					font-style: inherit;
				}
				a{
					color: #dfad00;
				}
				strong{
					font-family: $font-main-regular;
					font-weight: bold;
				}
				ul{
					list-style-image: url(data:image/png;base64,);
					li{
						color: #000;
						padding-left: 5px;
						padding-bottom: 8px;
						// @media (min-width: 768px){
						// 	padding-bottom: 23px;
						// 	// padding-left: 24px;
						// }
					}
				}
			}
		}
	}
}

.page-template-template-associates,
.page-template-template-partners{
	.partners-layout{
		padding-bottom: 80px;
	}
}

.page-template-template-contact{
	.contact-layout{
		.address,
		.phones,
		.fax{
			font-size: 16px;
			padding-bottom: 15px;
			@media (min-width: 768px){
				padding-bottom: 30px;
			}
			span{
				display: block;
			}
		}
		iframe{
			@media (min-width: 768px){
				padding-top: 30px;
			}
			width: 100%;
		}
	}
}

.page-template{
	&.page-template-template-principal{
		.seach-intro{
			font-size: 16px;
			color: #1b1b1b;
			padding-bottom: 40px;
		}
		select.categories{
			width: 430px;
			font-size: 16px;
			&:not([multiple]):not([size]){
				background-image: url("../img/dropdown.png");
				background-position: 97% center;
			}
			&:focus{
				border: 0;
				box-shadow: 0 0 15px #888888;
			}
			&:active, &:hover{
				outline:  #ca9f08;
			}
			option {
			    background-color: #e4b926;
			    color : #fff;
			    font-family: $font-main-regular;
			    font-size: 16px;
			    padding-top: 5px;
			    padding-bottom: 5px;
			    &:hover{
			    	background-color: #ca9f08;
			    }
			}
		}
		.lawyer-list{
			margin-top: 30px;
			// min-height: 270px;
			.lawyer-thumbnail{
				img{
					@include transition(0.5s);
				}
			}
			.lawyer-name{
				color: #000;
				font-size: 16px;
				font-family: $font-main-regular;
				margin-top: 9px;
			}
			&:hover{
				.lawyer-thumbnail{
					img{
						transform:scale(1.1);
						box-shadow: 0 0 15px #888888;
					}
				}
			}
		}
		.isotope{
			padding-bottom: 70px;
		}
	}
}
.page-template{
	.lawyer-list{
		margin-top: 0;
		min-height: 0;
		.lawyer-thumbnail{
			img{
				@include transition(0.5s);
			}
		}
		.lawyer-name{
			color: #000;
			font-size: 16px;
			font-family: $font-main-regular;
			margin-top: 9px;
		}
		&:hover{
			.lawyer-thumbnail{
				img{
					transform:scale(1.1);
					box-shadow: 0 0 15px #888888;
				}
			}
		}
	}
}

.page-template-template-timeline{
	.default-container{
		.page-title{
			padding-bottom: 0;
			margin-bottom: 0;
		}
	}
}

.page-template-template-thankyou{
	figure{
		padding-top: 75px;
	}
}

.home{
	.rev_slider{
		.tp-caption{
			>ul{
				li{
					font-size: 20px;
				}
			}
		}
	}
}

.page-template-template-about{
	.wrapper-holder{
		img{
			padding-bottom: 30px;
		}
		.read-more{
			color : #000;
			@include transition(0.5s);
			&:hover, &:visited, &:active, &:focus{
				color: #dfad00;
			}
			&:before{
				border-color: #dfad00;
			}
		}
	}
	
}

.main-layout{
	a{
		color: #dfad00;
		&:hover{
			color: #fcc505;
		}
	}
}

.uk-modal-body{
	h3{
		color: #dfad00;
		font-family: Georgia,serif;
		padding-bottom: 30px;
	}
	a{
		color: #dfad00;
	}
	ul{
		list-style-image: url(data:image/png;base64,);
		li{
			color: #000;
			padding-left: 5px;
			padding-bottom: 15px;
		}
	}
}